footer {
  font-family: "Exo", serif;
  font-optical-sizing: auto;
  padding: 6vh 0;
  background-color: #050227;
}
footer .social-circle {
  padding: 1rem 0;
}
footer .social-circle ul {
  padding-left: 0;
  list-style: none;
  display: flex;
  gap: 10px;
  margin-bottom: 0;
}
footer .social-circle ul li {
  display: inline-block;
}
footer .social-circle ul li:hover a.instagram svg path {
  transition: fill 0.5s ease-in-out;
  fill: #E4405F;
}
footer .social-circle ul li:hover a.linkedin svg path {
  transition: fill 0.5s ease-in-out;
  fill: #0077B5;
}
footer .social-circle ul li:hover a.twitter svg path {
  transition: fill 0.5s ease-in-out;
  fill: #1DA1F2;
}
footer .social-circle ul li:hover a.github svg path {
  transition: fill 0.5s ease-in-out;
  fill: #5C6BC0;
}
footer .social-circle ul li a {
  display: flex;
  align-items: center;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  transition: background-color 0.3s ease;
}
footer .social-circle ul li a svg {
  height: 26px;
}
footer #footer-info {
  text-align: right;
}
@media screen and (max-width: 600px) {
  footer #footer-info {
    padding-top: 2rem;
    text-align: left;
  }
}
footer #footer-info * {
  color: #fff;
}
footer #footer-info a {
  transition: all 0.5s ease;
}
footer #footer-info a:hover {
  color: #FF04FF;
}
footer #copyright * {
  color: #fff;
}
footer #copyright a {
  transition: all 0.5s ease;
}
footer #copyright a:hover {
  color: #FF04FF;
}

body:not(.home) header {
  background-color: #050227;
}

header {
  padding: 44px 0;
  position: fixed;
  left: 0;
  right: 0;
  z-index: 10;
}
header .logo {
  transform: rotate(338deg);
}
header.changebg {
  background-color: #050227;
}

.menu-button-wrapper {
  float: right;
}
@media screen and (max-width: 600px) {
  .menu-button-wrapper {
    position: fixed;
    float: right;
    right: 24px;
    top: 48px;
  }
}
.menu-button-wrapper .menu-button {
  transform: rotate(349deg);
  cursor: pointer;
}
.menu-button-wrapper .menu-button span {
  background-color: #FF04FF;
  display: block;
  height: 5px;
  margin: 5px 0;
  width: 54px;
  border-radius: 39px;
  transition: 0.4s;
}
.menu-button-wrapper .menu-button span:nth-child(1).change {
  -webkit-transform: rotate(-45deg) translate(-4px, 5px);
  transform: rotate(-45deg) translate(-4px, 5px);
  background-color: #5FD0E1;
}
.menu-button-wrapper .menu-button span:nth-child(2).change {
  opacity: 0;
}
.menu-button-wrapper .menu-button span:nth-child(3).change {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  transform: rotate(45deg) translate(-8px, -8px);
  background-color: #5FD0E1;
}

.full-menu-page {
  display: none;
  position: fixed;
  top: 129px;
  bottom: 0;
  background-color: #fff;
  left: 0;
  right: 0;
  z-index: 10;
  overflow-x: hidden;
  overflow-y: scroll;
}
@media screen and (max-width: 600px) {
  .full-menu-page {
    top: 128px;
  }
}
.full-menu-page.open {
  display: block;
}
.full-menu-page #menu-primary-menu {
  text-align: left;
}
.full-menu-page #menu-primary-menu li {
  padding-left: 0;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  list-style: none;
}
.full-menu-page #menu-primary-menu li a {
  font-size: 4rem;
  color: #4E2DF3;
  font-family: "Limelight", serif;
  letter-spacing: 3px;
  text-transform: uppercase;
}
@media screen and (max-width: 600px) {
  .full-menu-page #menu-primary-menu li a {
    font-size: 40px;
  }
}

.social .col-md-3 {
